<template>
    <div class="divMax">
        <span class="welcomeText">{{welcomeText}}</span>

        <div class="divMenuShow">
            <div class="Btn leftBtn" v-show="menuShowIndex>0"
                @click="$_SetShowIndex('-1')"></div>
            <ul class="ul-nolist clearFix menuList">
                <li v-for="(item,index) in menuList" v-bind:key="item.title" v-show="index==menuShowIndex">
                    <router-link v-bind:to="item.url">
                        <span>{{item.text}}</span>
                    </router-link>
                </li>
            </ul>
            <div class="Btn rightBtn" v-show="menuShowIndex<menuList.length-1"
                @click="$_SetShowIndex('+1')"></div>
        </div>

        
    </div>
</template>
<script>
    export default{
        name:'Welcome',
        created () {
            this.$_GetMenuList();
        },
        data () {
            return {
                welcomeText:'',
                menuList:[],
                menuShowIndex:0
            }
        },
        methods : {
            $_GetMenuList:function(){
                var self=this;
                self.$axios.get('/static/json/menu.json').then(d=>{
                    let data=d.data
                    if(data.status==200){
                        self.menuList=data.list
                    }
                });
            },
            $_SetShowIndex:function(x){
                var self=this
                let newShowIndex=self.menuShowIndex
                if(typeof x ==="string"){
                    newShowIndex=self.menuShowIndex+parseInt(x)
                }
                else{
                    newShowIndex=x
                }
                if(newShowIndex>=0 && newShowIndex<self.menuList.length){
                    self.menuShowIndex=newShowIndex
                }
            }
        }
    }
</script>
<style scoped>
.divMax{
    background-color: #F6F7FB;
}
.welcomeText{
    position: fixed;
    top:20%;
    left: 50%;
    transform: translateX(-50%);
}
.divMenuShow{
    position: absolute;
    top:50%;
    left:10%;
    right:10%;
    height:200px;
    transform: translateY(-50%);
    overflow: hidden;
    box-shadow: 0px 0px 10px #222;
}
.menuList{
    width:100%;
    height:100%;
    background-color: #FFF;
}
.Btn{
    position: absolute;
    top:0;
    bottom: 0;
    width:10%;
    min-width: 48px;
    z-index:1;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #eee;
    cursor: pointer;
    background-image: url(/static/img/icon/ArrowRight.png);
    /*opacity: 0.2;*/
}
.Btn:hover{
    background-size: 48px;
}
.leftBtn{
    left:0;
    transform: rotateY(180deg);
}
.rightBtn{
    right:0;
}
.menuList li{
    float: left;
    width:100%;
    height:100%;
}
.menuList li > a{
    display: block;
    width:100%;
    height:100%;
    color: rgb(0, 136, 186);
    font-size: 22px;
    /*background-image:linear-gradient(to right,rgb(0, 136, 186),rgb(0, 136, 186));*/
}
.menuList li > a > span{
    display: block;
    position: relative;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    max-width: 60%;
    text-overflow: ellipsis;
}
</style>



