<template>
	<!--PC & Mobile-->
	<div class="divMax"
		v-on:click="$_globalClick()">
		<span v-show="compShow" class="compTitle">{{compTitle}}</span>
		<!--菜单ICON-->
        <div title="菜单" v-bind:class="menuShow?'':'divMenuIconHidden'" class="divMenuIcon">
            <img class="imgMenu" v-bind:src="menuShow?'/static/img/icon/menu-on.png':'/static/img/icon/menu-off.png'" v-on:click.stop="$_triggerMenuShow" alt="Menu">
        </div>
		<!--菜单-->
        <transition name="menu">
            <div class="funcMenu" v-show="menuShow">
                <router-link to="/" v-on:click.native="$_closeRouter" title="首页"><img src="/static/img/icon/index.png" alt="Index"></router-link>
                <router-link to="/ImageProcess" title="图片处理"><img src="/static/img/icon/image.png" alt="Img"></router-link>
                <router-link to="/QRCode" title="生成二维码"><img src="/static/img/icon/qr-code.png" alt="QRCode"></router-link>
                <router-link to="/Map" title="百度地图"><img src="/static/img/icon/baiduMap.png" alt="Map"></router-link>
            </div>
        </transition>
		<router-view class="routerView"
            v-on:smallTipShow="$_callSmallTipShow"
			v-on:CompInit="$_routerCompInit"
			ref="eleRouterItem"> 
		</router-view>

	</div>
</template>
<script>
export default {
    name: 'Index',
    created: function() {},
    data() {
        return {
            curRouter: window.location.pathname,
            curTime: '',
            compShow: false,
            compTitle: '',
            menuShow: false
        };
    },
    methods: {
        $_routerCompInit: function(paras) {
            if (paras) {
                if (paras.url) {
                    this.curRouter = paras.url;
                    this.compShow = true;
                }
            }
            this.compTitle = paras && paras.title ? paras.title : '';
        },
        $_closeRouter: function() {
            this.curRouter = null;
            this.compShow = false;
            this.compTitle = '';
        },
        $_triggerMenuShow: function() {
            this.menuShow = !this.menuShow;
        },
        $_globalClick: function() {
            this.menuShow = false;
            if (
                this.$refs.eleRouterItem &&
                this.$refs.eleRouterItem.$_parentGlobalClick
            ) {
                this.$refs.eleRouterItem.$_parentGlobalClick();
            }
        },
        $_parentSmallTipCallBack:function(paras){
            this.menuShow = false;
            if (
                this.$refs.eleRouterItem &&
                this.$refs.eleRouterItem.$_parentSmallTipCallBack
            ) {
                this.$refs.eleRouterItem.$_parentSmallTipCallBack();
            }
        },
        $_callSmallTipShow:function(paras){
            this.$emit('smallTipShow',paras)
        }
			
    },
    components: {}
};
</script>
<style scoped>
.divMax{
    height: 100%;
    widows: 100%;
}
.menu {
    overflow: hidden;
}
.divMenuIcon{
    position: fixed;
    z-index: 101;
    right: 10px;
    top: 15%;
    padding: 5px;
    border: 1px solid #777;
    border-radius: 20px;
    background-color: #fff;
    overflow: hidden;
}
.divMenuIconHidden{
    transform: translateX(32px);
}
.imgMenu {
    display: block;
    width: 32px;
    height: 32px;
}
.funcMenu {
    position: fixed;
    z-index: 101;
    right: 17px;
    top: 15%;
    transform: translateY(45px);
    border: 1px #aaa;
    border-style: none none dotted none;
    box-shadow: 2px 2px 10px #aaa;
    background-color: #fff;
}
.funcMenu > a {
    display: block;
    padding: 5px 5px;
    cursor: pointer;
}
.funcMenu > a > img {
    display: block;
    width: 20px;
    height: 20px;
}
.funcMenu > a:hover {
    background-color: rgb(255, 242, 184);
}

.routerView {
    width:100%;
    height: 100%;
    overflow: hidden;
}
.compTitle {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    line-height: 32px;
    color: rgb(0, 136, 186);
}
.menu-enter-active{
    animation: menu-in 0.5s;
}
@keyframes menu-in {
    0%{
        max-height:0;
        overflow: hidden;
    }
    100%{
        max-height: 130px;
        overflow: hidden;
    }
}
</style>




